import React, { useState, useEffect, useContext } from "react";
import { TodoContext } from "./ToDoStore";
import styled, { css } from "styled-components";
import { makeStyles } from "@material-ui/core/styles";
import { display } from "@material-ui/system";
import Button from "@material-ui/core/Button";
import { IconButton } from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";

const ShowTodo = () => {
  const [toggle, setToggle] = useState(true);
  const { todoData, error, loading, dispatch } = useContext(TodoContext);

  const makeLiData = todos => {
    const arr = todos.map(data => {
      return (
        <LI onClick={() => onChangeHandler(data)} key={data.id}>
          {data.status === "todo" ? data.title : <del>{data.title}</del>}
          <IconButton id={data.id} onClick={e => onDeleteHandler(e, data)}>
            <DeleteIcon fontSize="small" />
          </IconButton>
        </LI>
      );
    });

    return arr;
  };

  const onChangeHandler = data => {
    dispatch({ type: "CHANGE_TODO", payload: data });
  };

  const onDeleteHandler = (e, data) => {
    e.stopPropagation();
    dispatch({ type: "DELETE_TODO", payload: data });
  };

  const makeLiComponent = data => {
    const todos = data;
    const isEmpty = !todos.length;
    let result;

    if (!isEmpty) {
      result = makeLiData(todos);
    } else {
      result = <li></li>;
    }
    return result;
  };

  const ModulateWindow = e => {
    toggle ? setToggle(false) : setToggle(true);
  };

  return (
    <ShowDIV>
      <DIV>
        <HEADER customAttr="test"> </HEADER>
        <Button color="secondary" onClick={e => ModulateWindow(e)}>
          {toggle ? "" : ""}
        </Button>
        <ul style={{ display: toggle ? "block" : "none" }}>
          {error && <li>  </li>}
          {loading && <li>...</li>}
          {!error && !loading && makeLiComponent(todoData)}
        </ul>
      </DIV>
    </ShowDIV>
  );
};

const DIV = styled.div`
  width: 45%;
  background-color: #dedcee;
  border-radius: 20px;
  margin-top: 3em;
`;

const HEADER = styled.h3`
  font-family: Monospace;
  text-align: center;
  font-weight: 400;
  font-size: 1.4em;
  padding-top: 0.5em;
`;

const ShowDIV = styled.div`
  display: flex;
  justify-content: space-around;
`;

const LI = styled.li`
  font-family: Monospace;
  font-size: 1.4em;
  font-weight: 100;
  height: 2.4em;
`;

export default ShowTodo;
