import React, { useState, useEffect, useRef } from "react";

const useFetch = (cbFunc, url, errorHandler) => {
  const [loading, setLoading] = useState(false);

  const fetchInitialData = async (cbFunc, url, errorHandler) => {
    try {
      setLoading(true);

      const response = await fetch(url);

      if (!response.ok) throw new Error(response.status); // resolved 404, 500.. 
      if (response === undefined) throw new Error("Promise(rejected)"); // Promise(rejected) 

      const jsonData = await response.json();

      setLoading(false);
      cbFunc(jsonData.body);
    } catch (errorMsg) {
      setLoading(false);
      errorHandler(errorMsg);
    }
  };

  useEffect(() => {
    fetchInitialData(cbFunc, url, errorHandler);
  }, []);

  return loading;
};

export default useFetch;
